import React from 'react';
import SkillList from './formatting/skill-list';
import{ css } from '@emotion/core';

const Skills = () => {
   const webList = ['HTML', 'CSS', 'JavaScript', 'React', 'Gatsby']
   const generalList = ['C', 'C++', 'Java', 'Python']
   const toolList = ['AWS', 'Bash', 'Unity', 'Flutter']

   return <div id="skills" className="main-section" css={css`
      text-align: center;
      flex: 1;

      #skills-body {
         display: flex;
         justify-content: center;
         flex-wrap: wrap;
         
         .column {
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            margin: 1rem 1rem;
            width: 250px;
            
            h2 {
               background: #383838;
               height: 50px;
               padding: 0.25rem;
               border-bottom: 1px solid #222;
            }

            > div {
               height: 120px;
               display: flex;
               justify-content: left;
               align-content: flex-start;
               margin: 0 0 0.25rem 0.5rem;
            }
         }
      }
   `}>
      <h1>My Skills</h1>
      <div id="skills-body">
         <div className="column">
            <h2>Web</h2>
            <SkillList skills={webList}/>
         </div>
         <div className="column">
            <h2>General</h2>
            <SkillList skills={generalList}/>
         </div>
         <div className="column">
            <h2>Tools</h2>
            <SkillList skills={toolList}/>
         </div>
      </div>
   </div>
};

export default Skills;